                        <i style={{ color: "grey", fontSize: '20px',cursor:'pointer' }} className="	fa fa-share-square-o " aria-hidden="true" onClick={() => {this.setState({showShare:!this.state.showShare})}} onMouseOver={() => {this.setState({showShare:true})}}></i> 
                        
