import { normalize } from 'normalizr';
import { conceptSchema } from '../schema/conceptSchema';
import getUniqueSortedAuthors from '../utils/getSortedAuthor';
const axios = require('axios');
var perspectivesByAuthor = {};
var sortedAuthors = [];
function createPerspectivesByAuthor(perspectives = null) {
    let authorPerspectives = Object.assign({}, perspectivesByAuthor);
    if (perspectives) {
        sortedAuthors.forEach(a => {
            authorPerspectives[a.id] = [];
        })
        perspectives.forEach(p => {
            if (!authorPerspectives[p.AuthorId]) {
                authorPerspectives[p.AuthorId] = [p.id]
            } else {
                authorPerspectives[p.AuthorId].push(p.id)
            }
        })
        console.log("returning array is ",authorPerspectives)
        return authorPerspectives
    }
}
function createNormalizePerspectives(perspectives = null) {
    let data={};
    if (perspectives) {
        perspectives.forEach(p => {
           data[p.id]=p;
        })
        console.log("returning normalized data is ",data);
        return data;
    }
}
