export function getAllPerspectivesByConcept(conceptId, page_no) {
    let conceptPage = {
        conceptId,
        page_no,
    }
    return dispatch => {
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/concepts/perspective`, { conceptPage })
            .then(data => {
                const { responseType } = data.data;
                if (responseType == "success") {
                    dispatch({
                        type: 'SET_DETAILED_PERSPECTIVE',
                        payload: data.data.perspectives
                    })
                }
            })
    }
}
export function getConceptPerspectivesByKeywords(keywordId,scroll=false,upScroll=false) {
    // console.log("got data",)
    console.log("zzz",keywordId)
    return dispatch => {
 
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/keywords/perspective`,{id:keywordId})
            .then(data => {
                const { responseType } = data.data;
                if (responseType == "success") {
                    
                    console.log("got scroll")
                    console.log("got scroll")
                    console.log("got scroll")
                    console.log("got scroll")
                    console.log("got scroll")
                    console.log("got scroll")
                    console.log("got scroll",data.data)
                    if(scroll)
                    {
                       if(data.data.data!==null)
                       {
                        data.data.data["perspectivesByAuthor"] = createPerspectivesByAuthorKeywords(data.data.data)
                        data.data.data["normalizedPerspectives"]=createNormalizePerspectivesKeywords(data.data.data);
                        if(upScroll)
                        {
                            dispatch({
                                type: 'SET_DETAILED_PERSPECTIVE_BY_KEYWORDS_SCROLL_UP',
                                payload: data.data.data
                            })
                        }
                        else
                        {
                            dispatch({
                                type: 'SET_DETAILED_PERSPECTIVE_BY_KEYWORDS_SCROLL',
                                payload: data.data.data
                            })
                        }
                       }
                    }
                    else
                    {
                        
                        data.data.data["perspectivesByAuthor"] = createPerspectivesByAuthorKeywords(data.data.data)
                        data.data.data["normalizedPerspectives"]=createNormalizePerspectivesKeywords(data.data.data);
                    
                        dispatch({
                            type: 'SET_DETAILED_PERSPECTIVE_BY_KEYWORDS',
                            payload: data.data.data
                        })
                    }
                    }

            })
    }
}
export function getPerspectivesByAuthors(authorIds, conceptId, merge = false) {
    let perspective = {};
    perspective["authorIds"] = authorIds;
    perspective["conceptId"] = conceptId;
    return dispatch => {
        
      axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspectives/authors`, { perspective })
            .then(async data => {
                const { responseType } = data.data;
                if (responseType == "success") {
                  
                    if (merge) {
                            data.data["normalizedPerspectives"]=createNormalizePerspectives(data.data.perspectives);
                          await  dispatch({
                                type: 'SET_DETAILED_PERSPECTIVE_BY_SCROLL',
                                payload: data.data
                            })
                    } else {
                        data.data["perspectivesByAuthor"] = createPerspectivesByAuthor(data.data.perspectives)
                        data.data["normalizedPerspectives"]=createNormalizePerspectives(data.data.perspectives);
                        dispatch({
                            type: 'SET_DETAILED_PERSPECTIVE_BY_AUTHOR',
                            payload: data.data
                        })
                    }
                }

            })
            .catch(err => {
                console.log(err)
            })
    }
}
export function getConceptPerspectivesByLikes(concept,offset,merge,isScroll=true)
{   
    if(merge)
    {
        let perspective={};
        perspective['conceptid']=concept.id
        perspective['offset']=offset
        return dispatch => {
            axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspectives/likes`,{perspective}).then(data => {
                data.data["normalizedPerspectives"]=createNormalizePerspectives(data.data.perspectives)                
               console.log("sending data",data.data);
               let finish=false;
               if(data.data.perspectives.length<100)
               {
                   finish=true
               } 


               dispatch({
                    type:'SET_NORMALIZED_PERSPECTIVES_BY_SCROLL',
                    payload:{data:data.data['perspectives'],finish:finish}
                })
            });
        }
    }
    else
    {
        let perspective={};
        perspective['conceptid']=concept.id
        perspective['offset']=offset
        return dispatch => {
            axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspectives/likes`,{perspective}).then(data => {
                data.data["normalizedPerspectives"]=createNormalizePerspectives(data.data.perspectives)
                console.log("sending data",data.data);
                let finish=false;
                if(data.data.perspectives.length<100)
                {
                    finish=true
                } 
 
                dispatch({
                    type:'SET_NORMALIZED_PERSPECTIVES',
                    payload:{data:data.data['perspectives'],isScrollLike:isScroll,finish:finish}
                })
            });
        }
    } 
}
