
import {normalize} from 'normalizr';
import {conceptClusterSchema} from '../schema/conceptClusterSchema';
const axios = require('axios');
export function flushConceptClusterState() {
    return dispatch => {
        dispatch({
            type: "FLUSH_CONCEPT_CLUSTER",
        })
    }
}
export function increasePerspectiveLike(id,index) {
    console.log("id ",id);
    let like = {};
    like.type = "increase";
    like.id = id;
    return dispatch => {
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspective/like`, { like })
            .then(res => {
                res.data.perspective.isLike = true;
                res.data.index=index;
                
                console.log("like is",res);
                dispatch({
                    type: 'CONCEPT_CLUSTER_PERSPECTIVE_LIKE',
                    payload: res.data
                })
            })
    }
}
export function decreasePerspectiveLike(id,index) {
    let like = {};
    like.type = "decrease";
    like.id = id;
    return dispatch => {
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspective/like`, { like })
            .then(res => {
                res.data.perspective.isLike = false;
                res.data.index=index;
                
                dispatch({
                    type: 'CONCEPT_CLUSTER_PERSPECTIVE_LIKE',
                    payload: res.data
                })
            })
    }
}
export function setClickedState(obj){
    return dispatch=>{
        dispatch({
            type:'CLICKED_STATE',
            payload:obj
        })
    }
}
