export function getConceptClusterFromApi(id,authors=[],scroll=false,upScroll=false) {
  
    return dispatch => {
        console.log("got data is ",authors)
        let idsArray=[];
        authors.map(key => {
            idsArray.push(key.id)
        })
        idsArray.reverse();
        if(scroll)
        {
            axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/conceptClusters/${id}?relationships=true`,idsArray).then(data => {
                const { responseType } = data.data;
                console.log("Got data on concept Cluster Page is ",data.data);
                if (responseType == 'success') {
                    const conceptDetailsFromApi = data.data.d;
                    // const normalizedData=normalize(conceptDetailsFromApi,conceptClusterSchema)
                    console.log("dispaching",conceptDetailsFromApi)
                    if(upScroll)
                    {

                        console.log("I am called",idsArray)
                        dispatch({
                            type: "REQUEST_SUCCESSFUL_UPSCROLL",
                            payload: conceptDetailsFromApi,
                        })

                    }
                    else
                    {
                        dispatch({
                            type: "REQUEST_SUCCESSFUL_SCROLL",
                            payload: conceptDetailsFromApi,
                        })
                    }
                    // console.log("before befor ",normalizedData.entities);
                    // dispatch({
                    //     type:'CONCEPT_CLUSTER_DETAIL_SCROLL',
                    //     payload:normalizedData.entities
                    // })
                }
                else {
                    dispatch({
                        type: "SERVER_ERROR"
                    })
                }
            }).catch(error => {
                dispatch({
                    type: "NETWORK_ERROR"
                })
                console.log('Error While fetching concept details from api', error);
            })
        }
        else
        {
          console.log("sesalfdjaskdf",id);  
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/conceptClusters/${id}?relationships=true`,idsArray).then(data => {
            const { responseType } = data.data;
            console.log("Got data on concept Cluster Page is ",data.data);
            if (responseType == 'success') {
                const conceptDetailsFromApi = data.data.d;
                console.log("data.data",conceptDetailsFromApi)
                // const normalizedData=normalize(conceptDetailsFromApi,conceptClusterSchema)
                // console.log("dispaching",conceptDetailsFromApi)
                dispatch({
                    type: "REQUEST_SUCCESSFUL",
                    payload: conceptDetailsFromApi,
                })
                
                // console.log("before befor ",normalizedData.entities);
                // dispatch({
                //     type:'CONCEPT_CLUSTER_DETAIL',
                //     payload:normalizedData.entities
                // })
            }
            else {
                dispatch({
                    type: "SERVER_ERROR"
                })
            }
        }).catch(error => {
            dispatch({
                type: "NETWORK_ERROR"
            })
            console.log('Error While fetching concept details from api', error);
        }) 
        }
    }
}