import React, { Component } from 'react';
import "./ConceptClustersPage.css";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { flushConceptClusterState,
     getConceptClusterFromApi,
     setClickedState,
     increasePerspectiveLike,
     decreasePerspectiveLike,
    getSortedAuthors } from "../../actions/conceptClustersActions";
import { setSelectedCategory, setSelectedItem, flushState, addTag } from "../../actions/navbaractions";
import{
    getUniqueSortedConceptsFromConceptCluster,
    getPerspectivesFromConceptCluster,
    getUniqueAuthorsFromConceptCluster
} from '../../selectors';
import { getAuthorGroups } from "../../utilities";
import Context from './Context';
import ReactLoading from 'react-loading';
import queryString from "query-string";
import Axios from 'axios';
import { Link } from "react-router-dom";
import { Redirect } from 'react-router-dom';
import { withRouter } from 'react-router-dom'
import $ from "jquery";
import loading from '../../../src/loading.gif';

import { disableBodyScroll, enableBodyScroll, clearAllBodyScrollLocks } from 'body-scroll-lock';



class ConceptClustersPage extends Component {
    index=0;
    topIndex=0;
    start=true
    targetElement = null;
    perspectiveLoading=false
    constructor(props) {
        super(props);
        this.state = {
            selectedOption: "",
            apiLoading: this.props.conceptClusters.apiLoading,
            apiServerError: this.props.conceptClusters.apiServerError,
            apiNetworkError: this.props.conceptClusters.apiNetworkError,
            /**
             * normalized states
             */
            authors:this.props.conceptClusters.authors,
            keywords:this.props.conceptClusters.keywords,
            sortedConcepts:this.props.sortedConcepts,
            sortedPerspectives:this.props.sortedPerspectives,
            uniqueAuthors:this.props.uniqueAuthors,
            perspectives:this.props.conceptClusters.perspective,
            sortByAuthor:"",
            viewport:""
             /**
             * end of normalized states
             */
        }
        this.onAuthorClick = this.onAuthorClick.bind(this);
        this.onLikeClick=this.onLikeClick.bind(this);
        this.handleScrollCallback = this.handleScrollCallback.bind(this);
        this.scrollDownPerspectiveHandler=this.scrollDownPerspectiveHandler.bind(this);


        if (this.props.navbar.selectedOption) {
            if (this.props.navbar.selectedOption.id) {
                if (this.props.navbar.selectedOption.category == "Concept-Clusters"){
                    this.props.getConceptClusterFromApi(this.props.navbar.selectedOption.id);
                }
            }
        }
    }
    getConceptCluster(cluster) {
        return new Promise((resolve, reject) => {
            Axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/conceptCluster`, { cluster }).then(data => {
                if(data.data.obj){
                    let result = data.data.obj.selectedOption;
                    resolve(result);
                }
                resolve(null);
            }).catch(err => {
                console.log(err);
            });
        });
    }
    showTargetElement = () => {
        // ... some logic to show target element
        
        // 3. Disable body scroll
        disableBodyScroll(this.targetElement);
      };
      
      hideTargetElement = () => {
        // ... some logic to hide target element
        
        // 4. Re-enable body scroll
        enableBodyScroll(this.targetElement);
      }
      componentDidUpdate(){
          console.log("sort sdby author is ",this.state.sortByAuthor);
          console.log("view port is ",this.state.viewport)
        // if (this.state.sortByAuthor) {
            const element = document.getElementById(this.state.viewport);
            if (element) {
                element.scrollIntoView({ behavior: 'auto' });
                $(window).scrollTop($(window).scrollTop()+200)
            }
            let id=this.state.sortByAuthor;
            console.log("id s"+"#"+this.state.sortByAuthor)
         
            // this.setState({
            //     sortByAuthor:""
            // })
        
        
        // }



          this.perspectiveLoading=false
        this.hideTargetElement();
      }
    componentDidMount()
    {
        // Axios.get(`${process.env.REACT_APP_SERVER}/api/rest/v1/conceptClusters/sortedAuthors/109`).then(data => {
        //     console.log("SortedAutorIds",data.data);
        // })
        console.log("Compoennt is mounted");
        this.targetElement = document.querySelector('#secondScr');

        window.addEventListener('scroll', this.handleScrollCallback, true);

    }
    async componentWillMount() {
        if (this.props.match.params.name) {
            if (this.props.match.params.name && queryString.parse(this.props.location.search).filters) {
                let filters = [];
                let queryFilters = queryString.parse(this.props.location.search).filters;
                if (typeof queryFilters === "string") {
                    filters.push(queryFilters);
                } else {
                    filters = queryFilters;
                }
                let author_group = await getAuthorGroups(filters);
                let cluster = {};
                cluster.name = this.props.match.params.name;
                cluster.groupIds = author_group.map(group => group.id);
                let result = await this.getConceptCluster(cluster);
                if (result) {
                    this.setState({
                        selectedOption: result
                    });
                    this.props.addTag(author_group);
                    this.props.setSelectedItem(result);
                } else {
                    return <Redirect to='/Message' />
                }
            } else {
                let cluster = {};
                cluster.name = this.props.match.params.name;
                cluster.groupIds = [];
                let result = await this.getConceptCluster(cluster);
                this.props.setSelectedItem(result);
                this.setState({
                    selectedOption: result
                });
                this.props.getConceptClusterFromApi(this.state.selectedOption.id);
            }
        }
    }
    scrollDownPerspectiveHandler()
    {
        this.perspectiveLoading=true
        let temp=this.state.uniqueAuthors.slice(this.index,10+this.index)
        this.index=11+this.index;
        console.log("scrolldown",temp);
        this.props.getConceptClusterFromApi(this.props.navbar.selectedOption.id,temp,true);
               
    }
    handleScrollCallback() {
        var windowHeight = $(window).height();
        var scrollHeight = $(window).scrollTop();
        console.log("ludo",scrollHeight)
        // this.showTargetElement();
        if(this.onceAuthorClicked)
        {
            this.onceAuthorClicked=false
            $(window).scrollTop($(window).scrollTop()-40);
        }
        if($(window).scrollTop()>this.prevScroll)
        {
            //Down Scrolling
         
         
         
            console.log((windowHeight + scrollHeight)+"vs"+ $('#favouriteSecond').innerHeight()/100*95)
         
         
            var rightBarHeight = $('#favouriteSecond').outerHeight();
            var rightBarTop = 30; 
            if(windowHeight  < rightBarHeight){ //Again including 30 because of .head
                rightBarTop = windowHeight - rightBarHeight;
            }
            
         
         
         
         
         
            if((windowHeight + scrollHeight) >=rightBarHeight)
            {
                this.showTargetElement();
        
                if(this.allowDownScrollCallBack)
                {
                    console.log("calling at "+(windowHeight + scrollHeight)+"and "+ $('#favouriteSecond').innerHeight()/100*50);
                    this.allowDownScrollCallBack=false;
                  if(!this.sortedByKewords)
                  {
                    this.scrollDownPerspectiveHandler();
                  }
                }


    
            }
            else
            {
                this.allowDownScrollCallBack=true;
            }
        }
        else
        {
            if($(window).scrollTop()==0)
            {
                
                console.log("Top index is ",this.topIndex)
                if(this.topIndex>0)
                {
                    if(!this.state.perspectiveLoading)
                    {
                        
                    console.log("First is ",this.state.sortedPerspectives[0].Author);
                    // this.setState({sortByAuthor:this.state.sortedPerspectives[0]})
                this.showTargetElement();
                    
                    console.log("calling scrollCallBack")
                    let authors=[];
                    console.log("Total is ",this.state.uniqueAuthors)
                    console.log("index - is ",this.topIndex)
                    if(this.topIndex-5>-1)
                    {
                        this.state.uniqueAuthors.slice(this.topIndex-5,this.topIndex).map(aut => authors.push(aut));
                    
                    }
                    else
                    {
                        this.state.uniqueAuthors.slice(0,this.topIndex).map(aut => authors.push(aut));
                    
                    }
                    console.log("before it was",authors)
                    authors.reverse();

                    console.log("New Array is ",authors);
                    // console.log("index - is ",author)
                    console.log("will be ",this.state.uniqueAuthors[this.topIndex-5])
                    // this.index=5+index;
                    console.log("setting viewport ",this.state.uniqueAuthors[this.topIndex])
                    this.setState({viewport:this.state.uniqueAuthors[this.topIndex].id,perspectiveLoading:true})
                    this.topIndex=this.topIndex-5
                    this.props.getConceptClusterFromApi(this.props.navbar.selectedOption.id,authors,true,true);
                    }
                    
                }     
            }       

        }
        this.prevScroll=$(window).scrollTop()




        //For First Column
        var rightBarWidth = $('.main').width()*.2 //20% of .main width
        var rightBarHeight = $('#firstInnerScr').outerHeight();
        // var rightBarOffset = $('.left').offset().left + $('.left').outerWidth();
        var rightBarTop = 30; //30 because .head is 30px high
        if(windowHeight  < rightBarHeight){ //Again including 30 because of .head
            rightBarTop = windowHeight - rightBarHeight;
        }
        
        
        if((windowHeight + scrollHeight) >= rightBarHeight){
            $('#firstScr').css({
                position:'fixed'
            })
            if($(window).height()<$('#firstInnerScr').innerHeight())
            {
                
                $('#firstScr').css({
                    bottom:'0'
                })  
            }
            $('.left').addClass("offset-md-3")
        
        }
        else{
             $('.right').css({
                position: 'static',
                left: '',
                bottom: ''
            })
            $('.left').removeClass("offset-md-3")
            
        }
        // For Third Column
        windowHeight = $(window).height();
         scrollHeight = $(window).scrollTop();
         rightBarWidth = $('.main').width()*.2 //20% of .main width
         rightBarHeight = $('#thirdInnerScr').outerHeight();
        //  rightBarOffset = $('.left').offset().left + $('.left').outerWidth();
         rightBarTop = 30; //30 because .head is 30px high
        if(windowHeight - 30 < rightBarHeight){ //Again including 30 because of .head
            rightBarTop = windowHeight - rightBarHeight;
        }
        // console.log("First Value is ",$(window).height());
        // console.log("Value is ",$('#thirdInnerScr').innerHeight());
        if((windowHeight + scrollHeight) >= rightBarHeight){
            $('#thirdInnerScr').css({
                position:'fixed'
            })
            if($(window).height()<$('#thirdInnerScr').innerHeight())
            {
                
                $('#thirdInnerScr').css({
                    bottom:'0'
                })  
            }
            // $('.left').addClass("offset-md-3")
        
        }
        else{
            // console.log("About to remove")
             $('#thirdInnerScr').css({
                position: 'static',
                left: '',
                bottom: ''
            })
            // $('.left').removeClass("offset-md-3")
            
        }
    
    
     
        
    }




    async componentWillReceiveProps(nextProps) {
        this.setState({perspectiveLoading:false})
        if (this.state.selectedOption != nextProps.navbar.selectedOption) {
            this.props.flushConceptClusterState();
            this.setState({
                selectedOption: nextProps.navbar.selectedOption,
                apiLoading: nextProps.conceptClusters.apiLoading,
                apiServerError: nextProps.conceptClusters.apiServerError,
                apiNetworkError: nextProps.conceptClusters.apiNetworkError,
            /**
             * normalized states
             */
                authors:nextProps.conceptClusters.authors,
                keywords:nextProps.conceptClusters.keywords,
                sortedConcepts:nextProps.sortedConcepts,
                sortedPerspectives:nextProps.sortedPerspectives,
                uniqueAuthors:nextProps.uniqueAuthors,
                perspectives:nextProps.conceptClusters.perspective
             /**
             * end of normalized states
             */

            })
        }
        else {
            this.setState({
                apiLoading: nextProps.conceptClusters.apiLoading,
                apiServerError: nextProps.conceptClusters.apiServerError,
                apiNetworkError: nextProps.conceptClusters.apiNetworkError,
                /**
             * normalized states
             */
                authors:nextProps.conceptClusters.authors,
                keywords:nextProps.conceptClusters.keywords,
                sortedConcepts:nextProps.sortedConcepts,
                sortedPerspectives:nextProps.sortedPerspectives,
                uniqueAuthors:nextProps.uniqueAuthors,
                perspectives:nextProps.conceptClusters.perspective
            /**
             * end of normalized states
             */
            })
        }
        if (this.props.navbar.tags.length !== nextProps.navbar.tags.length) {
           
            if (nextProps.navbar.selectedOption.category == "Concept-Clusters") {
                let cluster = {};
                cluster.name = nextProps.navbar.selectedOption.value;
                cluster.groupIds = nextProps.navbar.tags.map(group => group.id);
                let result = await this.getConceptCluster(cluster);
                console.log("Got This Only")
                if (result) {
                    this.setState({
                        selectedOption: result
                    });
                    this.props.getConceptClusterFromApi(nextProps.navbar.selectedOption.id,[],false);
                } else {
                   
                   this.props.history.push("/Message");
                }

            }
        }
        if( nextProps.conceptClusters.initial)
        {
            if(nextProps.navbar.selectedOption.id)
            {
            // console.log("kkkkkkkkk",this.props.navbar.selectedOption.id);
                this.start=true
                this.props.getSortedAuthors(this.props.navbar.selectedOption.id);
            }
            
        }
        console.log("redux",nextProps.navbar.selectedOption.category);
        if (nextProps.navbar.selectedOption.id && nextProps.navbar.selectedOption.category == "Concept-Clusters") {
            console.log("tututututututut",nextProps.uniqueAuthors)
            console.log("start",this.start);
            if(nextProps.uniqueAuthors && nextProps.uniqueAuthors.length>0 && this.start)
            {
                console.log("kkkkkkkkk",nextProps.uniqueAuthors)
            console.log("this.start",this.start)
            
                this.start=false;
                let temp=nextProps.uniqueAuthors.slice(this.index,10+this.index)
                this.index=11+this.index;
                console.log("sending this",temp)
                this.props.getConceptClusterFromApi(nextProps.navbar.selectedOption.id,temp,false);
                
            }
            // if(this.props.sortedAuthors && this.props.sortedAuthors.length>0)
            // {
            //     let temp=this.props.sortedAuthors.slice(this.index,5+this.index)
 
            //     console.log("First Times are")
            // }
            // this.props.getConceptClusterFromApi(nextProps.navbar.selectedOption.id);
        }

    }
    onLikeClick(id) {
        if (this.state.sortedPerspectives[id].isLike) {
            this.props.decreasePerspectiveLike(this.state.sortedPerspectives[id].id,id);
        }
        else {
            this.props.increasePerspectiveLike(this.state.sortedPerspectives[id].id,id);
        }
    }
    onAuthorClick(author) {
        let label = author.author_first + " " + author.author_last;
        let value = label;
        let id = author.auth_id;
        let born = author.born;
        let died = author.died;
        let category = "Authors";
        this.props.flushConceptClusterState()
        this.props.setSelectedItem({ label, value, id, born, died, category });
    }
    render() {
        return (
            <div className="" style={{ overflow: "scroll",paddingTop:50 }}>
                {this.state.selectedOption.id && this.state.selectedOption.category == "Concept-Clusters" && <div className="container-fluid">
                    {this.state.apiLoading && <div style={{ width: "100px", margin: "auto", marginTop: "30vh" }}>
                        <ReactLoading type={"balls"} color={"red"} />
                    </div>}
                    {!this.state.apiLoading && !this.state.apiNetworkError && !this.state.apiServerError && <div className="row">
                        <div className="col-xs-12 col-sm-12 col-md-3 right d-sm-none d-none d-md-block" id="firstScr" style={{ borderRadius: "3px", backgroundColor: "white",  overflow: "auto", textAlign: "" }}>
                           <div style={{backgroundColor:'white',}} id="firstInnerScr">
                           <div class="mb-2 bg-white text-center p-2">
                                <b><h4>{ this.state.selectedOption.label}</h4></b>
                            </div>
                            {this.state.sortedConcepts&&this.state.sortedConcepts.length > 0 && <div className="bg-white mt-2"><div className="" style={{ width: "150px", margin: "auto" }}>
                                { this.state.sortedConcepts.map(concept => {
                                    if (concept.name != undefined) {
                                        return <a onClick={() => {
                                            this.start=true;
                                            
                                            this.props.setClickedState(
                                                {
                                                    conceptClicked: true,
                                                    authorClicked: false,
                                                }
                                            )
                                        }} href={"#" + concept.id} style={{ cursor: "pointer", textDecoration: "none", color: "black", display: "block" }}><b>{concept.name}</b></a>
                                    }
                                })}
                            </div></div>}
                           </div>
                        </div>
                        <div className="col-12 col-sm-12 col-md-4 left " id="secondScr" style={{ borderRadius: "3px", backgroundColor: "white", overflow: "auto" }}>
                            <div id="favouriteSecond" style={{ backgroundColor: "white" }}>
                                {/* <div id="img" style={{ width: "458px", height: "280px", backgroundColor: "red" }}>
                                    <h4 style={{ zIndex: "3", padding: "10px", color: "white" }} className="pull-right">{this.state.selectedOption ? this.state.selectedOption.label : ""}</h4>
                                </div> */}
                                {/* <div style={{ marginTop: "10px", border: "1px solid white", paddingTop: "5px", borderRadius: "5px", width: "458px", textAlign: "center", backgroundColor: "white" }}>
                                    <label class="checkbox-inline" style={{ marginRight: "10px" }}>
                                        <input type="checkbox" value="" />Positive
                                        </label>
                                    <label class="checkbox-inline" style={{ marginRight: "10px" }}>
                                        <input type="checkbox" value="" />Critical
                                        </label>
                                    <label class="checkbox-inline" style={{ marginRight: "10px" }}>
                                        <input type="checkbox" value="" />Funny
                                        </label>
                                </div> */}
                                
                                {this.state.perspectiveLoading &&
                                    <div style={{position: 'absolute',display: 'flex',justifyContent: 'center', width: '100%'}}>
                                        <img src={loading} style={{ height: '20px' }}></img>
                                    </div>
                                    
                                }
                                {
                                    this.state.sortedPerspectives && this.state.sortedPerspectives.length > 0 && this.state.sortedPerspectives.map((data,index) => {
                                        
                                        return (
                                            <Context
                                                data={data}
                                                onAuthorClick={this.onAuthorClick}
                                                flushConceptClusterState={this.props.flushConceptClusterState}
                                                setSelectedItem={this.props.setSelectedItem}
                                                authors={this.state.authors}
                                                onLikeClick={this.onLikeClick}
                                                index={index}
                                            />
                                        );
                                    })
                                }
                                {this.perspectiveLoading &&
                                    <div style={{position: 'absolute',display: 'flex',justifyContent: 'center', width: '100%'}}>
                                    <img src={loading} style={{ height: '20px' }}></img>
                                    </div>
                                    
                                }
                            </div>
                        </div>
                        <div className="col-xs-12 col-sm-12 col-md-2 d-none d-sm-none d-md-block" id="thirdScr" style={{ borderRadius: "3px", backgroundColor: "white", overflow: "auto", borderLeft: "2px solid black", borderRight: "1px solid black", textAlign: "" }}>
                            <div style={{backgroundColor:'white'}} id="thirdInnerScr">
                            <ul  style={{"lineHeight": "0.9em",height: "auto",listStyle:"none"}}>
                            {this.state.uniqueAuthors && this.state.uniqueAuthors.length > 0 && this.state.uniqueAuthors.map((data,index) => {
                                return (
                                    <li  style={{ width: "150px", margin: "auto",backgroundColor:"", padding:"8px" }}><a onClick={() => {
                                       this.setState({
                                           sortByAuthor:this.state.uniqueAuthors[index].id,
                                           viewport:data.id
                                       })
                                        console.log("current index is ",data.id)
                                        let authors=[];
                                        console.log("Total is ",this.state.uniqueAuthors)
                                        console.log("index - is ",index)
                                        this.state.uniqueAuthors.slice(index-2,index).map(aut => authors.push(aut));
                                        this.state.uniqueAuthors.slice(index,2+index).map(aut => authors.push(aut));
                                        console.log("New Array is ",authors);
                                        console.log("index - is ",index)
                                        console.log("will be ",this.state.uniqueAuthors[this.index])
                                        this.index=2+index;
                                        this.topIndex=index-2
                                        console.log("inde xi s",index);
                                        console.log("top index is ",this.state.uniqueAuthors[this.topIndex])
                                        console.log("This.index is ",this.state.uniqueAuthors[this.index])
                                        authors.reverse();
                                        this.props.getConceptClusterFromApi(this.props.navbar.selectedOption.id,authors,false,false);
                                        {/* let arr=this.state.uniqueAuthors.slice() */}
                                        {/* console.log(data.id) */}
                                        {/* console.log("index is ",currentIndex) */}
                                        {/* this.props.setClickedState(
                                            {
                                                authorClicked: true,
                                                conceptClicked: false,
                                            } 
                                         ) */}
                                    }} style={{ color: "black", textDecoration: "none", cursor: "pointer",fontSize:14,height:"auto" }}  href={"#" + data.lastName}><b>{(data.lastName && data.firstName) ? (data.lastName + ", " + data.firstName) : (data.lastName ? data.lastName : (data.firstName ? data.firstName : ""))}</b></a></li>
                                );

                            })}
                            </ul>
                            </div>
                        </div>
                        <div className="col-xs-12 col-sm-12 col-md-3  d-none d-sm-none d-md-block" style={{ borderRadius: "3px", backgroundColor: "white", height: "89vh", overflow: "auto", borderLeft: "0px", textAlign: "center" }}>
                            <div className="text-center">
                                {/* This place is for adds */}
                            </div>
                        </div>
                    </div>
                    }
                </div>}
            </div>
        );
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        setSelectedCategory: bindActionCreators(setSelectedCategory, dispatch),
        setSelectedItem: bindActionCreators(setSelectedItem, dispatch),
        flushConceptClusterState: bindActionCreators(flushConceptClusterState, dispatch),
        getConceptClusterFromApi: bindActionCreators(getConceptClusterFromApi, dispatch),
        addTag: bindActionCreators(addTag, dispatch),
        setClickedState:bindActionCreators(setClickedState,dispatch),
        increasePerspectiveLike:bindActionCreators(increasePerspectiveLike,dispatch),
        decreasePerspectiveLike:bindActionCreators(decreasePerspectiveLike,dispatch),
        getSortedAuthors:bindActionCreators(getSortedAuthors,dispatch)
    }
}
const mapStateToProps = (state) => {
    console.log("state on concept cluster page is",state.conceptClusters)
    return {
        navbar: state.navbar,
        conceptClusters: state.conceptClusters,
        sortedConcepts:state.conceptClusters.sortedConcepts,
        sortedPerspectives: getPerspectivesFromConceptCluster(state),
        uniqueAuthors:state.conceptClusters.sortedAuthors

    }
}
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ConceptClustersPage));